body {
  font: 14px "Century Gothic", Futura, sans-serif;
}

ol, ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
	width:100%;
	height:100%;
	font-size:1.5vw;
	line-height:1.5vw;
	border:1px outset darkgrey;
	-webkit-touch-callout: none; /* iOS Safari */
	-webkit-user-select: none; /* Safari */
	-khtml-user-select: none; /* Konqueror HTML */
	-moz-user-select: none; /* Old versions of Firefox */
	-ms-user-select: none; /* Internet Explorer/Edge */
	user-select: none; /* Non-prefixed version, currently */
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
}

.no-padding{
	padding:0;
	margin:-1px;
}

.no-wrap{
	flex-wrap:nowrap;
}
.one-to-one-ap{
	aspect-ratio: 1 / 1;
	max-height: 50px;
	max-width: 50px;
}
.blueCell{
	color:blue;
	background:lightgrey;
}
.greenCell{
	color:green;
	background:lightgrey;
}
.redCell{
	color:red;
	background:lightgrey;
}
.darkBlueCell{
	color:darkblue;
	background:lightgrey;
}
.brownCell{
	color:brown;
	background:lightgrey;
}
.cyanCell{
	color:cyan;
	background:lightgrey;
}
.blackCell{
	color:black;
	background:lightgrey;
}
.greyCell{
	color:grey;
	background:lightgrey;
}
.bombCell{
	color:black;
	font-weight:bold;
	background:red;
}
.blankCell{
	color:lightgrey;
	background:lightgrey;
}
.unknownCell{
	background:darkgrey;
	border:5px outset darkgrey;
    -webkit-box-shadow: 5px 5px 15px rgba(0,0,0,0.4);
    -moz-box-shadow: 5px 5px 15px rgba(0,0,0,0.4);
}
.flagCell{
	background:darkgrey;
	color:red;
	border:5px outset darkgrey;
	-webkit-box-shadow: 5px 5px 15px rgba(0,0,0,0.4);
	-moz-box-shadow: 5px 5px 15px rgba(0,0,0,0.4);
}